
.container {
	max-width: 940px;
	margin: 0 auto; }

.header-page {
	&__top {
		display: flex;
		justify-content: space-between;
		align-items: center;
		padding: 10px 0; }

	&__center {
		display: flex;
		justify-content: space-between;
		align-items: center;
		padding: 10px 0; } }

.service-menu {
	display: flex;

	&__link {
		display: block;
		padding: 10px 15px; } }


.social-list {
	display: flex;

	&__btn {
		position: relative;
		display: block;
		width: 23px;
		height: 22px;
		background-image: url(../img/ico/fb.png);
		background-position: 50%;
		background-repeat: no-repeat; } }


